@media screen and (min-width: 0px) and (max-width: 300px){
    .top-bar{
        width: calc(300px - calc(var(--top-bar-margin) * 2));
    }

    .top-bar-user-btn {
        left: 240px !important;
    }
    .option-bubble{
        left: 113px !important;
    }

    .search{  
        width: calc(300px * 0.4);
        left: calc(150px - calc(300px * 0.4 / 2));
    }
}
@media screen and (min-width: 300px){
    .top-bar-user-btn {
        left: calc(100% - 40px - 20px);
    }
    .option-bubble{
        right: 5px;
    }

    .search{  
        width: var(--search-width);
        left: calc(50% - calc(var(--search-width) / 2));
    }


}

.top-bar{
    --top-bar-height: 52px;
    --top-bar-margin: 8px;

    margin: var(--top-bar-margin);
    height: var(--top-bar-height);

    border-radius: 10px;
    z-index: 3;

    background: #0adebe3d;
}

.top-bar-logo-and-name{

    position: relative;
    display: inline-block;

    width: 19%;
    max-width: 256.5px;

    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);

    left: 10px;

}

.top-bar-logo{
    --logo-btn-size: 44px;
    position: relative;

    width: var(--logo-btn-size);
    max-width: 256.5px;  
    top: 5px;
    left: 13px;
}

.top-bar-project-name{

    position: absolute;
    left: 70px;
    right: 260px;
    font-size: 35px;

    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

}

.top-bar-users-in-project {
    --user-btn-size: 40px;
    display: inline;
    margin: 3px;

    -webkit-user-select: none;
    border-radius: 100%;
    
    width: var(--user-btn-size);
    height: var(--user-btn-size);
    
}

.top-bar-users-button{
    --user-btn-size: 40px;
    cursor: pointer;

    position: absolute;
    -webkit-user-select: none;
    border-radius: 100%;
    background-color: #ffffff;
    color: #8b9694;
    box-shadow: 0px 0px 0px 2px#ffffff;
    width: var(--user-btn-size);
    height: var(--user-btn-size);
    left: calc(100% - 40px - 120px);

    top: calc(var(--top-bar-margin) + calc(calc(var(--top-bar-height) - var(--user-btn-size)) / 2));
}

.users-bubble {
    position: fixed;
    background: #dadee0;
    color: #FFFFFF;
    font-family: Arial;
    font-size: 20px;
    text-align: center;
    left: calc(100% - 40px - 117px);
    max-width: 250px;
    top: 77px;
    z-index:1;

    border-radius: 10px;
    padding: 10px;
}

.users-bubble:after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    z-index: 1;
    border-style: solid;
    border-color: #dadee0 transparent;
    border-width: 0 20px 20px;
    top: -20px;
    left: 50%;
    margin-left: -20px;
}


.top-bar-user-btn {
    --user-btn-size: 40px;
    cursor: pointer;

    position: absolute;
    -webkit-user-select: none;
    border-radius: 100%;
    box-shadow: 0px 0px 0px 2px#ffffff;
    width: var(--user-btn-size);
    height: var(--user-btn-size);
    left: calc(100% - 40px - 20px);

    top: calc(var(--top-bar-margin) + calc(calc(var(--top-bar-height) - var(--user-btn-size)) / 2));
    
}

.top-bar-code-btn{
    --user-btn-size: 40px;
    cursor: pointer;
    
    background-color: white;
    color: #8b9694;
    position: absolute;
    -webkit-user-select: none;
    border-radius: 100%;
    box-shadow: 0px 0px 0px 2px#ffffff;
    width: var(--user-btn-size);
    height: var(--user-btn-size);
    left: calc(100% - 120px - 140px);

    top: calc(var(--top-bar-margin) + calc(calc(var(--top-bar-height) - var(--user-btn-size)) / 2));
}

.code-bubble{
    position: fixed;
    background: #dadee0;
    color: #FFFFFF;
    font-family: Arial;
    font-size: 20px;
    text-align: center;
    width: 181px;
    height: 120px;
    border-radius: 10px;
    padding: 0px;
    z-index:99999;
    left: calc(100% - 331px);
    top: 77px;
}

.code-bubble:after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    z-index: 1;
    border-style: solid;
    border-color: #dadee0 transparent;
    border-width: 0 20px 20px;
    top: -20px;
    left: 50%;
    margin-left: -20px;
}
.project-code{
    background-color: #b3b6b6;
    position: relative;
    display: block;
    margin-top: 20px;
    margin-bottom: 10px;
    letter-spacing: 3px;
    
    width: 100%;
}
.copy-code-btn {
    outline: none;
    border:none;
    cursor: pointer;
    width:90%;
    height: 50px;
    border-radius: 30px;
    font-size: 20px;
    font-weight: 700;
    font-family: 'Lato', sans-serif;
    color:#fff;
    text-align: center;
    background: #0ADEBF;
    line-height: normal;
  }
  .copy-code-btn:hover {
    background:#2fdbb6;
  }


.search{
    --search-height: calc(var(--top-bar-height) * 0.8);
    --search-width: 40%;

    position: absolute;
    height: var(--search-height);
    border-radius: 10px;
    color: #555;
    box-shadow: inset 8px 8px 8px #0adabb34, inset -8px -8px 8px #d7fdf877;
    top: calc(var(--top-bar-margin) + calc(calc(var(--top-bar-height) - var(--search-height)) / 2));
}


.search input {
    position: relative;
    border: none;
    outline: none;
    width: calc(100% - 50px);
    
    font-size: 17px;
    background: none;
    bottom: 13px;
    color: #555;
}

.search-icon{
    position: relative;
    background: none;
    padding: 4px;
}

.option-bubble{
    position: fixed;
    background: #dadee0;
    color: #FFFFFF;
    font-family: Arial;
    font-size: 20px;
    text-align: center;
    width: 181px;
    border-radius: 10px;
    padding: 0px;
    z-index:99999;
    top: 77px;
}

.option-bubble:after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    z-index: 1;
    border-style: solid;
    border-color: #dadee0 transparent;
    border-width: 0 20px 20px;
    top: -20px;
    right: 14px;
    margin-left: -20px;
}

.disconect-code-btn {
    outline: none;
    border:none;
    cursor: pointer;
    width:90%;
    height: 50px;
    border-radius: 30px;
    font-size: 20px;
    font-weight: 700;
    font-family: 'Lato', sans-serif;
    color:#fff;
    text-align: center;
    background: #0ADEBF;
    line-height: normal;
    margin-top: 10px;
    margin-bottom: 10px;
}
.disconect-code-btn:hover {
    background:#2fdbb6;
}

