@media screen and (min-width: 0px) and (max-width: 1140px){
    .setup-btn {
        height: 50px;
        width: 50px;
        float: left;
        margin-right: 15px;
        background:#0ADEBF;
        position: relative;
        border-radius: 100%;
        font-family: system-ui;
        box-shadow: inset -2px -2px 5px #0cdabb, inset 2px 2px 5px #0cf5d2;

    }
    .setup-btn:hover {
        background-color : #31a7b4;
        box-shadow: inset -2px -2px 5px #19838f, inset 2px 2px 10px #38bdcc;
        cursor: pointer;
        transition: 0.2s;
    }
    .setup-btn:active{
        padding-left: 2px;
        box-shadow: inset 2px 2px 5px #19838f, inset -2px -2px 10px #3ac5d4;
        transition: 0s;
    }


    .Sname{
        visibility: hidden;
    }

    .Sdits {
        visibility: hidden;
    }

    .Sicon {
        position: relative;
        margin: 4px;
    }


    .project-options{
        position: fixed;
        bottom: 20px;
        right: 35px;
        z-index: 0;   
    }

}

@media screen and (min-width: 1140px){
    .setup-btn {
        min-height: 90px;
        width: 100%;
        background:#0ADEBF;
        position: relative;
        border-radius: 20px;
        color: #fff;
        font-family: system-ui;
        box-shadow: 13px 13px 20px #d1cfcb, -13px -13px 20px #ffffff, inset -2px -2px 5px #0cdabb, inset 2px 2px 5px #0cf5d2;
        margin-bottom: 24px;
    }
    .setup-btn:hover {
        background-color : #31a7b4;
        box-shadow: 13px 13px 20px #d1cfcb, -13px -13px 20px #ffffff, inset -2px -2px 5px #19838f, inset 2px 2px 10px #38bdcc;
        cursor: pointer;
        transition: 0.2s;
    }
    .setup-btn:active{
        padding-left: 2px;
        box-shadow: 13px 13px 20px #d1cfcb, -13px -13px 20px #ffffff, inset 2px 2px 5px #19838f, inset -2px -2px 10px #3ac5d4;
        transition: 0s;
    }


    .Sname{
        position: relative;
        top: -30px;
        left: 70px;
        font-size: 150%;
        font-family: inherit;
    }

    .Sdits {
        position: relative;
        top: -28px;
        left: 70px;
        margin-right: 80px;
        font-size: small;
        font-family: inherit;
        

    }

    .Sicon {
        position: relative;
        top: 15px;
        left: 17px;
    }


    .project-options{
        position: absolute;
        width: 35%;
        top: 15%;
        right: 0%;
        justify-content: center;
        align-items: center;

        z-index: 0;
        
        padding-left: 40px;
        padding-right: 40px;
        padding-top: 40px;
    }
}

