.login-container{
  margin:0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #555;
  background: #ecf0f3;
}


.login-div {
	width: 500px;
	height: 400px;
	padding: 60px 35px 35px 35px;
	border-radius: 40px;
	background: #ecf0f3;
  box-shadow: 13px 13px 20px #cbced1, -13px -13px 20px #ffffff;
  align-items: center;
  justify-content: center;
  
}

.logo {
    background:url("rsz_logo1.png");
    width:100px;
    height: 100px;
    border-radius: 50%;
    margin:0 auto;
    box-shadow: 
    /* logo shadow */
    0px 0px 2px #5f5f5f,
    /* offset */
    0px 0px 0px 5px #ecf0f3,
    /*bottom right */
    8px 8px 15px #a7aaaf,
    /* top left */
    -8px -8px 15px #ffffff
    ;
  }

.title {
  text-align: center;
  font-size: 28px;
  padding-top: 24px;
  letter-spacing: 0.5px;
}
.sub-title {
  text-align: center;
  font-size: 15px;
  padding-top: 7px;
  letter-spacing: 3px;
}

  
.fields {
  width: 100%;
  padding: 75px 5px 5px 5px;
}
.fields input {
  border: none;
  outline:none;
  background: none;
  font-size: 18px;
  color: #555;
  padding:20px 10px 20px 5px;
}

.fields svg {
  height: 22px;
  margin:0 10px -3px 25px;
}

.signin-google-button {
  position: fixed;
  float: left;
  height: 0px;
  width: 0px;
  visibility: hidden;
  z-index: -9999;
}

.signin-button {
  position: relative;
  outline: none;
  border:none;
  cursor: pointer;
  width:70%;
  height: 60px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Lato', sans-serif;
  color:rgb(0, 0, 0);
  text-align: center;
  background: #ffffff;
  box-shadow: 3px 3px 8px #b1b1b1,
              -3px -3px 8px #ffffff;
  transition: 0.3s;
  left :15%;

}
.signin-button:hover {
  color : #ffffff;
  background:#2fdbb6;
  
}
.signin-button:active {
  background:#1da88a;
}
.link {
  padding-top: 20px;
  text-align: center;
}
.link a {
  text-decoration: none;
  color:#aaa;
  font-size: 15px;
}

.google-icon{
  background: none;
  position: relative;
  left :-2%;
}