.tree {
  position: absolute;
  bottom:0px;
  top:68px;
  left: 50px;
  padding: 5px;
  bottom: 0px;
  background-color: #ebebeb;
  font-size: 1.4em;
}

.tree > div {
  height: auto !important; 
}

.m-tree{
  top: 110px;
  /*height: calc(100% - 100px);*/
  bottom: 0px;
  width: calc(var(--file-tree-width) - var(--strip-width));
  position: fixed;
  overflow-x: hidden;
  overflow-y: auto;
}

/* width */
.m-tree::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.m-tree::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
} 

/* Handle */
.m-tree::-webkit-scrollbar-thumb {
  background: hsl(167, 50%, 50%); 
  border-radius: 10px;
}

/* Handle on hover */
.m-tree::-webkit-scrollbar-thumb:hover {
  background: hsl(167, 46%, 42%); 
}

.m-tree.placeholder {
  border: 1px dashed #1385e5;
}




.m-node .inner {
  color: #ffffff;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}

.m-node .node {
  display: inline-block;
  width: 100%;
  padding: 4px 5px;
}

