.container{
	height: 100%;
	bottom: 0px;
	overflow: auto;
}

@media screen and (min-width: 0px) and (max-width: 300px){
	.list-container{
		position: relative;
		width: 260px;
		height: calc(100% - 33px);
		bottom: 0px;
		margin-left: 20px;
		overflow: auto;
	}

 
	/* width */
	.list-container::-webkit-scrollbar {
		width: 20px;
	}
	
	/* Track */
	.list-container::-webkit-scrollbar-track {
		box-shadow: inset 0 0 5px grey; 
		border-radius: 10px;
	} 

	/* Handle */
	.list-container::-webkit-scrollbar-thumb {
		background: hsl(167, 50%, 50%); 
		border-radius: 10px;
	}

	/* Handle on hover */
	.list-container::-webkit-scrollbar-thumb:hover {
		background: hsl(167, 46%, 42%); 
	}

	.btn {
		height: 50px;
		width: 260px;
		background:inherit;
		position: relative;
		border-radius: 60px;
		margin-bottom: 5px;
	}
	.btn:hover {
		background-color: #e8fdfe;
		cursor: pointer;
	}

	.options{
		height: 35px;
		width: 35px;
		border: none;
		position: absolute;
		outline: none;
		border-radius: 40px;
		top: 15%;
		right: 10px;
	}
	.options-icon{
		position: absolute;
		font-size: 150%;
		top: -57%;
		right: 17%;
	}
	.options:hover{
		background: #56ffd569;
	}


	.Pname {
		position: absolute;
		width: 65%;
		top: 15%;
		left: 20%;
		font-size: 150%;
		font-family: sans-serif;
		
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}

	.Pcreator {
		visibility: hidden;
	}
	.Pdate {
		visibility: hidden;
	}
	
	.Pname-tag {
		position: relative;
		display:inline;
		left: calc(240px * 0.30);
		font-family: sans-serif;
	}
	.Pcreator-tag {
		visibility: hidden;
		width: 0px;
		height: 0px;
	}
	.Pdate-tag {
		visibility: hidden;
		width: 0px;
		height: 0px;
	}

	.seperetor{
		background: #000000;
		height: 1px;
		width: 240px;
		margin-left: 20px;
		margin-right: 20px;
		margin-bottom: 10px;
	}

	.app-icon {
		position: absolute;
		top: 10%;
		left: 6px;
	}

}

