.options-container{
	position: absolute;
	display: block;
	background:#ffffff;
	z-index: 2;
	border: 2px #000000;
	border-style: solid;
	left: -50px;

}

.rename , .clone, .download, .delete{
	display: block;
	padding:10px ;
}

.download{
	text-decoration:none
}

.rename:hover, .clone:hover, .download:hover{
	background:#d8d8d8;
}

.delete:hover{
	background:#ff0000;
	color: white;
}

.rename:active, .clone:active, .download:active{
	background:#bdbdbd;
}

.delete:active{
	background:#cc0000;
	color: white;
}